$star-color: #7dfff2;
$star-speed: 2;
$star-screen-width: 3000;
$star-density: 400;
$star-delay: 2;

body{
    background-color:#1d303a;
    background-repeat: repeat-x;
}
@function generate-random-stars ($screen-width, $color, $density){

    $h-shadow: random($screen-width);
    $v-shadow: random($screen-width);

    $stars: '#{$h-shadow}px #{$v-shadow}px #{$color}';
    @for $i from 2 through $density {
        $h-shadow: random($screen-width);
        $v-shadow: random($screen-width);

        $stars: '#{$stars}, #{$h-shadow}px #{$v-shadow}px #{$color}';
    }
    @return unquote($stars);
}

@mixin stars-content($screen-width, $color, $density, $speed, $delay) {
    width: 2px;
    height: 2px;
    border-radius: 50%;
    opacity: 0;
    box-shadow: generate-random-stars($screen-width, $color, $density);
    animation-name: glowing-stars;
    animation-duration: $speed+s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: linear;
    animation-delay: $delay+s;
}



//Change colors for multi-colors star groups

#stars-group-1{
    @include stars-content($star-screen-width, $star-color, $star-density, $star-speed, 0*$star-delay);
}

#stars-group-2{
    @include stars-content($star-screen-width, $star-color, $star-density, $star-speed, 0.1*$star-delay);
}

#stars-group-3{
    @include stars-content($star-screen-width, $star-color, $star-density, $star-speed, 0.2*$star-delay);
}

#stars-group-4{
    @include stars-content($star-screen-width, $star-color, $star-density, $star-speed, 0.3*$star-delay);
}

#stars-group-5{
    @include stars-content($star-screen-width,$star-color, $star-density, $star-speed, 0.4*$star-delay);
}

#stars-group-6{
    @include stars-content($star-screen-width, $star-color, $star-density, $star-speed, 0.5*$star-delay);
}


@keyframes glowing-stars {
    0%   {opacity: 0;}
    50%  {opacity: 1;}
    100% {opacity: 0;}
}

