.container{
    .content{
        .single-post{
            padding-bottom: 50px;
            padding-top: 50px;
            padding-left: 20px;
            padding-right: 20px;
            background-color: $base-white;
            border-radius: 10px;
            img{
                max-width: 400px;
                display: block;
                margin-left: auto;
                margin-right: auto;
                width: 50%;
                margin-bottom: 20px;
                margin-top: 10px;
                border-radius: 10px;

            }
            p{
                font-size: 1.1rem;
            }
            h2{
                text-align: center;
            }
            h3{
                text-align: center;
                margin-top: 30px;
            }
            a{
                text-decoration: none;
                color: $base-black;
                display: inline-block;
                font-style: italic;
                border-bottom: 1px solid $base-color;
                line-height: 0.9;
                transition: background-color 0.1s cubic-bezier(.33,.66,.66,1);
            }
            a:hover{
                background-color: rgba(1,160,228,0.3);
                border-bottom: none;
            }

        }
    }
}